/* tabs wrapper content */
.tabs{

}
.tabs nav{
  margin-bottom: 25px;
  text-align: center;
}

  /* tabs list */
  .tabs__list{
    margin: 0;
    padding: 0;
    list-style: none;
  }
    .tabs__list li{
      display: inline-block;
      margin-right: 10px;
    }
    .tabs__list a{
      text-decoration: none;
      color: var(--color-white);
    }
  
/* tab block */
.block{
  width: 100%;
  padding: 15px;
  overflow: hidden;
  
  display: -ms-flexbox;
  
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: stretch;
      align-items: stretch;
  
  color: var(--color-white);
  
  border-radius: 5px;
  background: var(--block-bg);
  -webkit-box-shadow: 0 1px 7px 1px rgba( var(--color-black), .17);
          box-shadow: 0 1px 7px 1px rgba( var(--color-black), .17);
}

/* players wrapper */
.players_wrapper{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

/* player */
.player{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;

  margin-bottom: 25px;

  color: var(--color-white);

  margin-top: -200px;
}
  .player:first-child{
    margin-top: 0;
  }

  /* player mediaqueries */
  @media(min-width: 992px){
    .player{
      min-width: 70%;
      -ms-flex: 0 0 70%;
          flex: 0 0 70%;
    }
  }

  /* player align */
  .player-left{
    -ms-flex-item-align: start;
        align-self: flex-start;
  }
    .player-left .player__image{
      margin-right: 50px;
    }
  .player-right{
    -ms-flex-item-align: end;
        align-self: flex-end;
    -ms-flex-pack: end;
        justify-content: flex-end;
  }
    .player-right .player__image{
      margin-left: 50px;
      -ms-flex-order: 2;
          order: 2;
    }
    .player-right .player__content{
      -ms-flex-order: 1;
          order: 1;
    }

  /* player image bg */
  .player-0 .player__image{
    background: url('./../../images/p0.png') no-repeat 50%;
  }
  .player-1 .player__image{
    background: url('./../../images/p1.png') no-repeat 50%;
  }
  .player-2 .player__image{
    background: url('./../../images/p2.png') no-repeat 50%;
  }
  .player-3 .player__image{
    background: url('./../../images/p3.png') no-repeat 50%;
  }
  .player-4 .player__image{
    background: url('./../../images/p4.png') no-repeat 50%;
  }
  .player-5 .player__image{
    background: url('./../../images/p5.png') no-repeat 50%;
  }
  .player-6 .player__image{
    background: url('./../../images/p6.png') no-repeat 50%;
  }
  .player-7 .player__image{
    background: url('./../../images/p7.png') no-repeat 50%;
  }

  /* player image */
  .player__image{
    width: 320px;
    height: 500px;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;

    -webkit-transition: all .5s linear;

    -o-transition: all .5s linear;

    transition: all .5s linear;
  }
  .player:hover .player__image img{
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
    -webkit-filter: grayscale(30%);
            filter: grayscale(30%);
  }
  .player__image img{
    width: 100%;
    height: 100%;
    -o-object-fit: contain;
       object-fit: contain;

    -webkit-filter: grayscale(60%);

            filter: grayscale(60%);

    -webkit-transition: all .25s ease-in-out;

    -o-transition: all .25s ease-in-out;

    transition: all .25s ease-in-out;
  }

  /* player content */
  .player__content{

  }

  /* player name, surname, nickname */
  .player__name{

  }
  .player__surname{
  }
  .player__nickname{
    vertical-align: super;
    font-size: 18px;
    color: var(--color-highlight);
  }
  .divider{
    padding: 10px;
    color: var(--color-gray);
  }


